* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
body {
  background-color: bisque;
}
.App {
  display: flex;
  flex-direction: column;
  min-height: 90vh;
  align-items: center;
  justify-content: center;
  padding: 15px;
}

.calculator {
  width: 100%;
  max-width: 400px;
  border-radius: 15px;
  overflow: hidden;
  background-color: white;
  margin-top: 3rem;
}

.display {
  text-align: right;
  background-color: #2c3333;
  font-size: 40px;
  padding: 20px;
  color: white;
  overflow-wrap: break-word;
}

span {
  font-size: 20px;
  color: lightgray;
}

button {
  appearance: none;
  border: none;
  outline: none;
  font-size: 20px;
  padding: 20px;
  cursor: pointer;
  color: white;
  transition: 0.4s;
}

button:hover {
  opacity: 0.9;
}

.operators {
  display: flex;
}

.operators button {
  flex: 1 1 20%;
  background-color: #395b64;
  font-weight: 700;
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  flex: 1 1 33.33%;
  max-width: 33.33%;
  background-color: #2c3333;
}

h1 {
  margin: 10px;
  font-size: 3rem;
  color: #395b64;
  background-color: #2c3333;
  padding: 20px;
  border-radius: 15px;
}

h1 p {
  font-size: 15px;
  text-align: right;
  font-weight: 200;
  letter-spacing: 1px;
  font-style: italic;
}
